<template>
  <div id="translateOutput">
	  <img src="static/img/loading.gif" class='loading ' v-bind:class='flag'>
	  <ul class="list-unstyled">
		  <li v-for='text in arrayTranslate'><h4>{{text}}</h4></li>
	</ul>
<!--    <h5>{{arrayTranslate}}</h5>-->
  </div>
</template>

<script>

export default {
  name: 'arrayTranslate',
  props:['arrayTranslate', 'flag']
}
</script>

<style>
	.loading{
		display: block;
		margin:0 auto;
		width:100px;
		height:70px;
	}
	.hide{
		display: none;
	}
	.show{
		display: block
	}
	li{
		font-size: 24px;
	}
</style>



